import { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import "../css/detail-page.css";
import axios from "axios";

function DetailPage() {
  let { id } = useParams();
  const [title, setTitle] = useState("");
  const [name, setName] = useState("");
  const [createDate, setCreateDate] = useState("");
  const [skillStack, setSkillStack] = useState("");
  const [workField, setWorkField] = useState("");
  const [maxPeople, setMaxPeople] = useState("");
  const [endDate, setEndDate] = useState("");
  const [content, setContent] = useState("");
  useEffect(() => {
    //   
    axios.get(`/api/board/detail/${id}`).then((res) => {
      setTitle(res.data.title);
      setName(res.data.name);
      setCreateDate(res.data.createDate.slice(0, 10));
      setSkillStack(res.data.skillStack);
      setWorkField(res.data.workField);
      setMaxPeople(res.data.maxPeople);
      setEndDate(res.data.endDate);
      setContent(res.data.content);
    });
  }, []);
  return (
    <>
      <div class="detail_board">
        <table>
          <tr>
            <td class="title">{title}</td>
          </tr>
          <tr>
            <td class="createDate">
              <b> </b>
              {createDate}
            </td>
            <td class="author">
              <b> </b>
              {id}
            </td>
            <td class="workField">
              <b> </b>
              {workField}
            </td>
            <td class="skillStack">
              <b> </b> {skillStack}
            </td>
            <td class="maxPeople">
              <b> </b> {maxPeople}
            </td>
            <td>
              <b>  </b>
              {endDate}
            </td>
          </tr>
          <tr class="content">
            <td>{content}</td>
          </tr>
          <div>
            <button
              onClick={() => {
                axios
                  .get(`/api/board/delete?id=${id}`)
                  .then((res) => {
                    window.location.href = "/";
                    alert(" ");
                  })
                  .catch((err) => {
                    console.log(err);
                  });
              }}
            >
              
            </button>
            <button>
              <Link to={`/update/${id}`} style={{ textDecoration: "none" }}>
                
              </Link>
            </button>
          </div>
        </table>
      </div>
    </>
  );
}

export default DetailPage;
